<template>
  <div id="wrapper" ref="wrapper">
    <slot></slot>
  </div>
</template>

<script>
import IScroll from 'iscroll/build/iscroll-probe'
import { mapGetters } from 'vuex'
export default {
  name: 'ScrollVue',
  data: function () {
    return {
      iscroll: null,
      routerPath: ''
    }
  },
  methods: {
    // 
    scrolling (fn) {
      this.iscroll.on('scroll', function () {
        fn(this.y)
      })
    },
    refresh () {
      setTimeout(() => {
        this.iscroll.refresh()
      }, 200)
    },
    scrollTo (x, y, time) {
      this.iscroll.scrollTo(x, y, time)
    },
    currentY () {
      return this.iscroll.y
    },
    maxScrollY () {
      return this.iscroll.maxScrollY
    },
    isScrollClick () {
      const userAgent = navigator.userAgent
      if (/iPhone|iPad|iPod|Macintosh/i.test(userAgent)) return false
      if (/Chrome/i.test(userAgent)) return (/Android/i.test(userAgent))
      if (/Android/i.test(userAgent)) return true
    },
    scrollStart (fn) {
      this.iscroll.on('scrollStart', function () {
        fn()
      })
    },
    scrollEnd (fn) {
      this.iscroll.on('scrollEnd', function () {
        fn()
      })
    }
  },
  mounted () {
    this.iscroll = new IScroll(this.$refs.wrapper, {
      // click
      click: this.isScrollClick(),
      mouseWheel: true,
      scrollbars: false,
      // 
      probeType: 3,
      // 
      scrollX: false,
      scrollY: true,
      disablePointer: true,
      disableTouch: false,
      disableMouse: true
    })
    // 1 
    /*
    * MutationObserver
    * mutationList
    * observer
    * */
    var observer = new MutationObserver((mutationList, observer) => {
      this.iscroll.refresh()
    })
    // 2 
    const config = {
      // attributes: true, // 
      childList: true, // 
      subtree: true, //  false
      attributeFilter: ['height', 'offsetHeight'] // 
      // characterData: true // 
      // attributeOldValue: true, //  attributes 
      // characterDataOldValue: true //  characterData 
    }
    // 3 
    /*
    * 1
    * 2
    * */
    observer.observe(this.$refs.wrapper, config)
  },
  computed: {
    ...mapGetters([
      'isShowMiniPlayer'
    ])
  },
  watch: {
    // miniPlayer /iScroll
    // wrapperbottom@include position_bottom;
    isShowMiniPlayer (newValue) {
      document.documentElement.setAttribute('data-mini', newValue)
      this.refresh()
    }
  },
  //  keep-alive 
  activated () {
    // 
    this.refresh()
  }
}
</script>

<style scoped lang="scss">
  #wrapper{
    width: 100%;
    height: 100%;
  }
</style>
